.performance {
    background-color: #597eb3;
    padding: 44px 0px 50px;
    text-align: center;
    /* IE9, iOS 3.2+ */
    background-image: url(data:image/svg+xmlbase64,);
    background-image: -webkit-gradient(linear, 0% 0%, 100% 0%,color-stop(0, rgb(89, 147, 178)),color-stop(0.996, rgb(89, 105, 179)),color-stop(0.996, rgb(89, 147, 178)),color-stop(1, rgb(89, 105, 179)));
    /* Android 2.3 */
    background-image: -webkit-repeating-linear-gradient(left,rgb(89, 147, 178) 0%,rgb(89, 105, 179) 99.6%);
    /* IE10+ */
    background-image: repeating-linear-gradient(to right,rgb(89, 147, 178) 0%,rgb(89, 105, 179) 99.6%);
    background-image: -ms-repeating-linear-gradient(left,rgb(89, 147, 178) 0%,rgb(89, 105, 179) 99.6%); }

.performance__title {
	font-family: $omnes-light;
	color: #fff;
	font-weight: 300;
	font-size: 42px;
	letter-spacing: -1px; }

.performance__descr {
	color: #fff;
	font-size: 18px;
	font-family: $helveticanuen-light;
	font-weight: 300; }

.performance__descr {
    margin-top: 6px;
    letter-spacing: -0.4px; }

.performance__list {
    margin-top: 35px; }

.performance__list-item-wrap {
    border: 1px solid #517fa1;
    border-radius: 15px;
    padding: 18px 0px 12px; }

.performance__list-item-image {
    height: 54px; }

.performance__list-item-descr {
    color: #fff;
    font-family: $helveticanuen-mid;
    font-size: 13px;
    margin-top: 9px; }


