.helped-events {

    background-color: #7369ae;
    text-align: center;
    padding: 35px 0px 48px;
    /* IE9, iOS 3.2+ */
    background-image: url(data:image/svg+xml;base64,);
    background-image: -webkit-gradient(linear, 0% 0%, 100% 0%,color-stop(0, rgb(197, 227, 239)),color-stop(0, rgb(86, 105, 174)),color-stop(1, rgb(143, 104, 174)),color-stop(1, rgb(142, 212, 241)));
    /* Android 2.3 */
    background-image: -webkit-repeating-linear-gradient(left,rgb(197, 227, 239) 0%,rgb(86, 105, 174) 0%,rgb(143, 104, 174) 100%,rgb(142, 212, 241) 100%);
    /* IE10+ */
    background-image: repeating-linear-gradient(to right,rgb(197, 227, 239) 0%,rgb(86, 105, 174) 0%,rgb(143, 104, 174) 100%,rgb(142, 212, 241) 100%);
    background-image: -ms-repeating-linear-gradient(left,rgb(197, 227, 239) 0%,rgb(86, 105, 174) 0%,rgb(143, 104, 174) 100%,rgb(142, 212, 241) 100%); }


.helped-events__title {
	font-family: $omnes-light;
	font-weight: 300;
	font-size: 34px;
	color: #fff;
	width: 90%;
	margin: 0 auto;
	line-height: 43px; }

.helped-events__item-title {
	font-family: $helveticanuen-bold;
	font-size: 16px;
	color: #fff;
	font-weight: 700; }

.helped-events__item-descr {
	font-family: $helveticanuen;
	font-size: 13px;
	color: #d9d3ff;
	margin: 0 auto;
	margin-top: 10px;
	width: 80%; }


